<style lang="less" scoped>
  .side-wrap {
    .logo {
      height: 60px;
      padding: 18px 0;
      padding-left: 17px;
      .icon {
        width: 24px;
        height: 24px;
        vertical-align: middle;
      }
      span {
        font-size: 15px;
        color: #fff;
        vertical-align: middle;
      }
    }
    .el-menu {
      border: none;
      .el-menu-item{
        i{
          color: #647288;
        }
        &.is-active{
          background-color: #3f8ffb !important;
          i{
            color: #fff;
          }
        }
      }
      .el-menu-item, .el-submenu__title {
        color: #ffffff;
        &:hover {
          background-color: #398CFF !important;
        }
        span{
          font-size: 14px;
        }
        height: 50px;
        line-height: 50px;
      }
      @{global} .hasSub.el-submenu .el-submenu__title{
        padding-left: 10px !important;
      }
      .noSub.el-menu-item {
        padding-left: 10px !important;
      }
      .el-menu-item{
        padding-left: 54px !important;
      }
    }
  }
  .nav-bottom {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 20px;
    font-size: 0;
  }
  .nav-user-head {
    display: inline-block;
    margin-right: 12px;
    width: 36px;
    height: 36px;
    text-align: center;
    background: #fff;
    border-radius: 36px;
    overflow: hidden;
    vertical-align: top;
    &::before {
      content: "";
      display: inline-block;
      width: 0;
      height: 36px;
      vertical-align: middle;
    }
    img {
      max-width: 100%;
      max-height: 100%;
      vertical-align: middle;
    }
  }
  .nav-user-con {
    display: inline-block;
    padding-right: 14px;
    width: 112px;
  }
  .nav-user-name {
    display: block;
    font-size: 14px;
    color: #fff;
  }
  .nav-user-position {
    font-size: 12px;
    color: #67D89D;
  }
  .el-menu-vertical-demo {
    height: calc(100vh - 137px);
    overflow: auto;
  }
  .nav-dropdown {
    font-size: 0;
  }
  .nav-icon {
    position: absolute;
    right: 0;
    top: 11px;
    color: #627287;
    font-size: 14px;
  }
</style>

<template>
  <div class="side-wrap">
    <div class="logo">
      <div>
        <svg class="icon" aria-hidden="true"><use xlink:href="#icon-userSys"></use></svg>
        <span>用户权限管理系统</span>
      </div>
    </div>
    <el-menu
      router
      :default-active="this.$route.path"
      class="el-menu-vertical-demo"
      background-color="#06193D"
      text-color="#fff"
      active-text-color="#fff"
      unique-opened>
      <template v-for="(nav, nindex) in navList">
        <el-submenu v-if="nav.children" :index="`${nindex}`" :key="nindex" class="hasSub">
          <template slot="title">
            <svg class="icon" aria-hidden="true"><use :xlink:href="'#icon-'+(nav.icon !== 'default'? nav.icon: 'menu')"></use></svg>
            <span>{{nav.name}}</span>
          </template>
          <el-menu-item v-for="(snav, sindex) in nav.children" :key="`${nindex}-${sindex+1}`" route :index="snav.url">
            <template slot="title">
              <span>{{snav.name}}</span>
            </template>
          </el-menu-item>
        </el-submenu>
        <el-menu-item v-else :key="`${nindex}`" :index="nav.url" class="noSub">
          <svg class="icon" aria-hidden="true"><use :xlink:href="'#icon-'+(nav.icon !== 'default'? nav.icon: 'menu')"></use></svg>
          <span>{{nav.name}}</span>
        </el-menu-item>
      </template>
    </el-menu>
    <div class="nav-bottom">
      <el-dropdown class="nav-dropdown" @command="loginOut">
        <div class="el-dropdown-link">
          <div class="nav-user-head">
            <img :src="user.avatar_url">
          </div>
          <div class="nav-user-con">
            <span class="nav-user-name">{{user.user_name}}</span>
            <span class="nav-user-position">{{user.position}}</span>
          </div>
          <i class="nav-icon el-icon-caret-bottom"></i>
        </div>
        <el-dropdown-menu style="width: 160px;" slot="dropdown">
          <el-dropdown-item command="loginOut">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import api from '../../api'
export default {
  data () {
    return {
      getRoutePath: '',
      nav: {},
      navList: [],
      user: this.$store.state.userInfo || [],
      storageList: []
    }
  },
  beforeMount () {
    api.getRbacMenu().then(res => {
      this.navList = res.result || []
      this.navList.push(
        {
          children: [{
            name: '修改密码',
            url: '/account-management/change-password',
            level: 2
          }, {
            name: '账号密码管理',
            url: '/account-management/account-setup',
            level: 2
          }, {
            name: '员工登录方式配置',
            url: '/account-management/login-setup',
            level: 2
          }, {
            name: '外部用户登录方式配置',
            url: '/account-management/other-login-setup',
            level: 2
          }],
          code: '7c8c0efd-495e-4c18-8e54-013131a2a620',
          icon: 'default',
          level: 1,
          name: '用户管理',
          url: '#'
        })
    })
  },
  methods: {
    loginOut () {
      api.loginOut().then(res => {
        if (res.code === 200) {
          // window.location = 'http://10.125.20.45:8081/login'
        }
      })
    },
    handle (item) {
    }
  }
}
</script>



