<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
#aside {
  width: 200px !important;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  background-color: #06193D;
  z-index: 9;
}
#container {
  margin-left: 200px;
}
</style>
<template>
<div>
  <el-aside id="aside">
    <side-bar></side-bar>
  </el-aside>
  <el-container id="container">
    <div class="container">
      <navigation>
        <router-view/>
      </navigation>
    </div>
  </el-container>
</div>
</template>

<script>
import SideBar from '../../components/side/bar'
import TopHeader from '../../components/header'

export default {
  components: {
    SideBar, TopHeader
  },
  data () {
    return {
      dialogTableVisible: false,
      keyword: '',
      isMust: false
    }
  },
  computed: {
    user () {
      let user = this.$store.state.common.user
      user.photo = 'user-avatar.png'
      user.title = [
        '',
        '中心总监',
        '中心管理层',
        user.workDuty,
        'HR管理员',
        '项目管理员'
      ][user.role] || user.workDuty
      return user
    }
  },
  methods: {
    close () {
      this.dialogTableVisible = false
    },
    userOperate (command) {
      switch (command) {
        case '1':
          localStorage.removeItem('token')
          localStorage.removeItem('user')
          // 清除首页数据
          this.$store.commit('SET_CHARTDATA', {})
          this.$router.push('/login')
          break
        case '0':
          this.dialogTableVisible = true
          break
        case '3':
          this.$router.push('/personal-data-set')
          break
      }
    }
  },
  beforeCreate () {
  },
  created () {
  }
}
</script>



